import React, { useState } from 'react'
import './index.css'

const App = ()=>{
  const [values,setValues] = useState({
    firstName:'',
    lastName:'',
    email:''
  })

  const [submitted,SetSubmitted]= useState(false)
  const [valid,SetValid]= useState(false)
  const handleFirstNameInputChange = (event) => {
    setValues({...values, firstName: event.target.value})

  }
  const handleLastNameInputChange = (event) => {
    setValues({...values, lastName: event.target.value})

  }
  const handleEmailInputChange = (event) => {
    setValues({...values, email: event.target.value})

  }
  
  const handleSUbmit = (event)=>{
event.preventDefault()
if(values.firstName && values.lastName && values.email){
  SetValid(true)
}
SetSubmitted(true)
  } 
  return(
    <div className="form-container">
      <form className="register-form" onSubmit={handleSUbmit}>
        {submitted && valid ? <div className="sucess-message">Successfully Registered</div>:null}
    <input 
    onChange={handleFirstNameInputChange}
    value={values.firstName}
    className="form-field"
    placeholder="First Name"
    name="firstName" />
    {submitted & !values.firstName ?<span>Please Enter a Name </span>:null }
      <input 
      onChange={handleLastNameInputChange}
      value={values.lastName}
      className="form-field"
        placeholder="Last Name"
        name="lastName"
         />
         {submitted & !values.lastName ?<span>Please Enter a Name </span>:null }
        <input 
        onChange={handleEmailInputChange}
        value={values.email}
        className="form-field"
        placeholder="Email"
        name="email" />
         {submitted & !values.email ?<span>Please Enter a email id </span>:null } 
        <button className="form-field"
        >Register</button>

      </form>
    </div>
  )
}
export default App